
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import value2name from 'vux/src/filters/value2name.js'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'

import XButton from '../../../node_modules/vux/src/components/x-button/index.vue'

export default {
mounted: function () {
},
components: {
XButton,
XHeader,
Group,
Panel,
Divider,
Card,
Cell,
FormPreview
},
data () {
return {
type: '6',
isHasAddress: true,
Addresses: [{
id: '11',
receivedName: '李晓明',
tel: '13520580169',
addressArray: ['430000', '430400', '430407'],
addressShowName: '',
detail: '清河小营东路15号院中国电力科学研究院'
}]
}
},
methods: {
addAddress () {
this.$router.push({name: 'addAddress'})
},
editAddress (index) {
this.$router.push({name: 'editAddress', params: {address: this.Addresses[index]}})
},
deleteAddress (index) {
console.log('delete' + index)
}
},
computed: {
showAddresses: function () {
if (this.Addresses != null) {
var show = this.Addresses
for (var i = 0; i < show.length; i++) {
const idArray = show[i].addressArray
const name = value2name(idArray, ChinaAddressV4Data)

show[i].addressShowName = name
}
return show
} else {
this.isHasAddress = false
}
}
},
watch: {}
}



