
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import Home from './index_views/Home'
import Performance from './index_views/Performance'
import Wallet from './index_views/Wallet'
import Salary from './index_views/Salary'
import Consume from './index_views/Consume'

export default {
mounted: function () {
},
components: {
XHeader,
Group,
Tabbar,
TabbarItem,
Cell,
Scroller,
'home': Home,
'performance': Performance,
'wallet': Wallet,
'salary': Salary,
'consume': Consume
},
data () {
return {
currentView: 'home'
}
},
methods: {
changeView (view) {
this.currentView = view
}
}
}



