
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Card from 'vux/src/components/card/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import Message from '../../components/msg'

export default {
components: {
XHeader,
XInput,
Group,
XButton,
Card,
FormPreview,
Message,
Selector,
Cell
},
data () {
return {
bonusCoin: 2000,
selectCard: '',
getNumber: '',
tel: '',
cards: [{
id: '2',
ownerName: '李晓明',
bankName: '招商银行',
cardNumber: '**** **** **** 7228'
}, {
id: '4',
ownerName: '李晓明',
bankName: '招商银行',
cardNumber: '**** **** **** 7228'
}, {
id: '8',
ownerName: '李晓明',
bankName: '招商银行',
cardNumber: '**** **** **** 7228'
}],
formView: [{
label: '提现金额',
value: ''
}, {
label: '手续费',
value: '￥10元'
}, {
label: '实际到账金额',
value: ''
}],
getCashButton: [{
style: 'primary',
text: '提现',
onButtonClick: (name) => {
this.getCashSubmit()
}
}]
}
},
methods: {
getCashSubmit () {
console.log('get cash')
},
addCard () {
this.$router.push({name: 'addBankCard'})
}
},
computed: {
isInput: function () {
return this.getNumber >= 100 && this.getNumber < this.bonusCoin
},
isHasCards: function () {
return (this.cards && this.cards.constructor === Array && this.cards.length !== 0)
},
cardsInfo: function () {
if (this.cards && this.cards.constructor === Array && this.cards.length !== 0) {
const infos = new Array([])
const tmpcards = this.cards
for (let i = 0; i < tmpcards.length; i++) {
infos[i] = {
key: tmpcards[i].id,
value: tmpcards[i].ownerName + ' ' + tmpcards[i].bankName + ' ' + tmpcards[i].cardNumber.substr(tmpcards[i].cardNumber.length - 4, 4)
}
}
return infos
} else {
return null
}
}
},
watch: {



getNumber (newValue) {
this.formView[0].value = '￥' + newValue + '元'
this.formView[2].value = '￥' + (newValue - 10) + '元'
}
}
}



