
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Card from 'vux/src/components/card/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'

import Message from '../../components/msg'
import GoeConfig from '../../../config/goe'

export default {
components: {
XHeader,
XInput,
Group,
XButton,
Card,
FormPreview,
Message
},
data () {
return {
bonusCoin: 2000,
consumeCoin: 2000,
isEnableAccess: true,
price: GoeConfig.goe.price,
reConsumePrice: GoeConfig.goe.reConsumePrice,
buttons2: [{
style: 'primary',
text: '购买',
onButtonClick: (name) => {
alert(`clicking ${name}`)
}
}]
}
},
methods: {},
computed: {
list2: function () {
const list = [{
label: '商品原价',
value: '报单币￥' + this.price
}, {
label: '重销优惠价格',
value: '报单币￥' + this.reConsumePrice
}]
return list
},
isEnableBuy: function () {
return Number(this.consumeCoin) < Number(this.reConsumePrice)
}
}
}



