import XButton from 'vux/src/components/x-button/index.vue'

import PerformanceView from '../../components/ViewPerformance'
export default {
mounted: function () {
},
data () {
return {
findAccount: '',
currentView: '当前查看用户: admin (A:12 B:12 C:0)',
ViewUserModel: {
currentViewAccount: '',
currentCountA: '',
CurrentCountB: '',
currentCountC: '',
departUserA: {
userAAccount: '',
userACountA: '',
userACountB: '',
userACountC: ''
},
departUserB: {
userBAccount: '',
userBCountA: '',
userBCountB: '',
userBCountC: ''
},
departUserC: {
userCAccount: '',
userCCountA: '',
userCCountB: '',
userCCountC: ''
}
},
