src: require('../../assets/images/partABC/A.png'),
title: '用户A',
desc: 'A:12  B:12  C:0'
}, {
isHasUser: false,
src: require('../../assets/images/partABC/B.png'),
title: '用户B',
desc: 'A:12  B:12  C:0'
}, {
isHasUser: false,
src: require('../../assets/images/partABC/C.png'),
title: '用户C',
desc: 'A:12  B:12  C:0'
}]
}
},
components: {
XButton,
XHeader,
Group,
PerformanceView,
Divider,
Card,
Cell,
Search
},
methods: {
setFocus () {
this.$refs.search.setFocus()
},
resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult (val) {
console.log('on-change', val)
this.results = val ? this.getResult(this.value) : []
},
onSubmit () {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
},
createUser () {
console.log('create user event')
},
viewUser (item) {
if (item.isHasUser) {
console.log('view user event' + item.title)
} else {
console.log('do nothing')
}
},
viewMyPerformance () {
console.log('view my performance')
}
}
}



