
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Card from 'vux/src/components/card/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'

import GoeConfig from '../../../config/goe'
import Message from '../../components/msg'

export default {
mounted: function () {
},
components: {
XHeader,
XInput,
Group,
XButton,
Card,
FormPreview,
XNumber,
Message
},
data () {
return {
productCoin: 4000,
exchangeNumber: 1,
orderInfo: [{
label: '兑换数量',
value: '1'
}, {
label: '总计',
value: '￥680'
}],
buttons2: [{
style: 'primary',
text: '兑换',
onButtonClick: (name) => {
alert(`clicking ${name}`)
}
}]
}
},
methods: {
changeNumber (val) {
this.orderInfo[1].value = '￥' + val * GoeConfig.goe.price
}
},
computed: {
showConvertNumber: function () {
return '积分￥' + GoeConfig.goe.price
},
isEnableBuy: function () {
return this.productCoin > GoeConfig.goe.price
},
MaxNumber: function () {
return parseInt(this.productCoin / GoeConfig.goe.price)
}
},
watch: {
}
}



