
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Card from 'vux/src/components/card/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Msg from 'vux/src/components/msg/index.vue'

import Message from '../../components/msg'
export default {
mounted: function () {
},
components: {
XHeader,
XInput,
Group,
XButton,
Card,
FormPreview,
Search,
Divider,
Panel,
Msg,
Message
},
data () {
return {
consumeCoin: 2000,
isFinded: false,
isNotFinded: false,
transferNumber: '',
type: '1',
list: [{
src: require('../../assets/images/person.png'),
title: '张三 [组长]',
desc: 'A:20  B:20:  C:0'
}],
list2: [{
label: '收款用户',
value: 'admin'
}],
buttons2: [{
style: 'primary',
text: '转账',
onButtonClick: (name) => {
alert(`clicking ${name}`)
}
}]
}
},
methods: {
findUser () {
this.isFinded = true
}
},
computed: {
isInput: function () {
return (Number(this.transferNumber) > 0 && this.transferNumber <= this.consumeCoin)
}
},
watch: {
transferNumber (newValue, oldValue) {
this.showConvertNumber = '￥' + newValue
}
}
}



