
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Card from 'vux/src/components/card/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'


export default {
mounted: function () {
},
components: {
XHeader,
XInput,
Group,
XButton,
Card,
FormPreview
},
data () {
return {
bonusCoin: 20000,
consumeCoin: 1000,
convertNumber: '',
showConvertNumber: '',
list: [{
label: '转换后奖金余额',
value: '2000'
}, {
label: '转换后报单币余额',
value: '3000'
}],
buttons2: [{
style: 'primary',
text: '转换',
onButtonClick: (name) => {
alert(`clicking ${name}`)
}
}]
}
},
methods: {
},
computed: {
isInput: function () {
return (Number(this.convertNumber) > 0 && this.convertNumber <= this.bonusCoin)
}
},
watch: {
convertNumber (newValue, oldValue) {
this.showConvertNumber = '￥' + newValue
this.list[0].value = '￥' + (this.bonusCoin - newValue)
this.list[1].value = '￥' + (Number(this.consumeCoin) + Number(newValue))
}
}
}



